import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import {FormControl} from '@angular/forms';
import { MatDialog} from '@angular/material';
import {DialogEditTang,DialogDeleteSetting,DialogEditClassRoom, DialogEditRoom,DialogEditStaff} from './dialog-setting/dialog-setting.component';
declare var $:any;

@Component({
    selector: 'app-setting-content',
    templateUrl: './setting-content.component.html',
    styleUrls: ['./setting-content.component.css']
})

export class SettingContentComponent implements OnInit{

    selected = new FormControl(0);

    constructor(
        private route: ActivatedRoute,
        public dialog: MatDialog,
    ){}

    editTang(){
        const dialogRef = this.dialog.open(DialogEditTang,{width: '500px'});
    
        dialogRef.afterClosed().subscribe(result => {
          console.log('change tang closed');
        });
      }

    deleteSetting(){
        const dialogRef = this.dialog.open(DialogDeleteSetting,{width: '500px'});
    
        dialogRef.afterClosed().subscribe(result => {
          console.log('delete closed');
        });
    }

    editClassRoom(){
        const dialogRef = this.dialog.open(DialogEditClassRoom,{width: '500px'});

        dialogRef.afterClosed().subscribe(result => {
            console.log('edit class room');
        });
    }

    editRoom(){
        const dialogRef = this.dialog.open(DialogEditRoom,{width: '500px'});

        dialogRef.afterClosed().subscribe(result =>{
            console.log('edit room');
        });
    }

    editStaff(){
        const dialogRef = this.dialog.open(DialogEditStaff,{width: '500px'});

        dialogRef.afterClosed().subscribe(result =>{
            console.log('edit staff');
        });
    }

    ngOnInit(){
        this.route.paramMap.subscribe(params => {
            this.selected.setValue(+params.get('tabNumber'));
        });

        $(function() {

            autoSize();
            $(window).resize(function(event) {
                autoSize();
            });

            $('.mat-tab-label').click(function(event) {
                autoSize();
            });

            function autoSize(){
                let window_width = $(window).width();

                if(window_width < 1200){
                    $('.extended-service').removeClass('flex-row').addClass('flex-column');
                    $('.extended-service .child-service').removeClass('flex-50').addClass('flex-100');
                } else if(window_width >= 1200){
                    $('.extended-service').removeClass('flex-column').addClass('flex-row');
                    $('.extended-service .child-service').removeClass('flex-100').addClass('flex-50');
                }
            }
        })
    }
}