import { Component, OnInit, Inject } from '@angular/core';
import { FormBuilder } from '@angular/forms';
import { MatDialog, MatDialogRef } from '@angular/material';
import {FormControl} from '@angular/forms';
import {ThemKhachComponent} from './them-khach/them-khach.component';
import {ThemDoanComponent} from './them-khach/them-doan.component';
declare var $:any;

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit {

  changePasswordForm;

  constructor(
    private formBuilder: FormBuilder,
    public dialog: MatDialog,
  ) { }

  addKhachLe(){
    const dialogRef = this.dialog.open(ThemKhachComponent,{panelClass: 'myapp-no-padding-dialog', width:'100%', maxWidth:'100%'});

    dialogRef.afterClosed().subscribe(result => {
      console.log('add khach le closed');
    });
  }

  addKhachDoan(){
    const dialogRef = this.dialog.open(ThemDoanComponent,{panelClass: 'myapp-no-padding-dialog', width:'100%', maxWidth:'100%'});

    dialogRef.afterClosed().subscribe(result => {
      console.log('add khach doan closed');
    });
  }

  openChangePasswordDialog(){
    const dialogRef = this.dialog.open(changePasswordDialog,{ width:'500px', maxWidth:'95%'});

    dialogRef.afterClosed().subscribe(result => {
      console.log('change password closed');
    });
  }

  openWorkDialog(){
    const dialogRef = this.dialog.open(ListWorkDialog,{width: '90%', maxHeight: '95%'});

    dialogRef.afterClosed().subscribe(result => {
      console.log('work closed');
    });
  }

  ngOnInit() {

    this.changePasswordForm = this.formBuilder.group({
      old_password:'',
      new_password:'',
      retype_password:''
    });
    
    $('a.open-menu-child').click(function(event) {
      $(this).next().addClass('active');
      $('.overplay-white').addClass('active');
      return false;
    });

    //on-off thanh menu
    $('#on-off-menu').click(function(event) {
      $('.sidenav').addClass('active');
      $('.overplay').addClass('active');
      $('.overplay').css({"z-index": 90});
    });
    //on-off thanh menu setting
    $('#on-off-setting').click(function(event) {
      $('.sidenav-setting').addClass('active');
      $('.overplay').addClass('active');
    });
    
  }

  changePasswordSubmit(customerData){
    console.warn('your order has been submitted', customerData);
  }

}





@Component({
  selector: 'dialog-change-password',
  templateUrl: './dialog-change-password.html',
  styleUrls: ['./header.component.css']
})
export class changePasswordDialog{
  constructor(
    public dialogRef: MatDialogRef<changePasswordDialog>
  ){}
  onNoClick(){
    this.dialogRef.close();
  }
}





export interface SelectInput {
  value: string;
  viewValue: string;
}

@Component({
  selector: 'work-Dialog',
  templateUrl: './diablog-work.html',
  styleUrls: ['./header.component.css']
})

export class ListWorkDialog{

  date = new FormControl(new Date());
  serializedDate = new FormControl((new Date()).toISOString());

  user_adds: SelectInput[] = [
    {value: '0', viewValue: 'Tất cả'},
    {value: '1', viewValue: 'Tèo'},
    {value: '2', viewValue: 'Tũn'},
    {value: '3', viewValue: 'Tý'}
  ];

  addWork(){
    const dialogRef = this.dialog.open(AddListWorkDialog,{width: '500px', maxHeight: '90%'});

    dialogRef.afterClosed().subscribe( result => {
      console.log('work closed');
    });
  }

  constructor(
    public dialogRef: MatDialogRef<ListWorkDialog>,
    public dialog: MatDialog,
  ){}

}




@Component({
  selector: 'add-List-Work',
  templateUrl: './dialog-add-list-work.html',
  styleUrls: ['./header.component.css']
})

export class AddListWorkDialog implements OnInit{

  addworkForm;

  date = new FormControl(new Date());
  serializedDate = new FormControl((new Date()).toISOString());

  user_adds: SelectInput[] = [
    {value: '0', viewValue: 'Tất cả'},
    {value: '1', viewValue: 'Tèo'},
    {value: '2', viewValue: 'Tũn'},
    {value: '3', viewValue: 'Tý'}
  ];

  list_rooms: SelectInput[] = [
    {value: '001', viewValue: '001'},
    {value: '002', viewValue: '002'},
    {value: '002', viewValue: '003'},
    {value: '101', viewValue: '101'},
    {value: '102', viewValue: '102'},
    {value: '301', viewValue: '301'},
  ];

  constructor(
    public dialogRef: MatDialogRef<AddListWorkDialog>,
    private formBuilder: FormBuilder,
  ){}

  ngOnInit(){
    this.addworkForm = this.formBuilder.group({
      room: '',
      date: '',
      user_add: '',
      manipulation: ''
    });
  }

  addWorkSubmit(customerData){
    console.warn('your order has been submitted', customerData);
  }

}
