import {Component} from '@angular/core';
import {MatDialogRef, MatDialog} from '@angular/material';

@Component({
    selector: 'dialog-edit-tang',
    templateUrl: './dialog-edit-tang.html',
    styleUrls: ['./dialog-setting.component.css']
})
export class DialogEditTang{
    
    constructor(
        public dialogRef: MatDialogRef<DialogEditTang>,
    ){}

}


@Component({
    selector: 'dialog-delete-setting',
    templateUrl: './dialog-delete-setting.html',
    styleUrls: ['./dialog-setting.component.css']
})
export class DialogDeleteSetting{
    
    constructor(
        public dialogRef: MatDialogRef<DialogDeleteSetting>,
    ){}

}


@Component({
    selector: 'dialog-edit-class-room',
    templateUrl: './dialog-edit-class-room.html',
    styleUrls: ['./dialog-setting.component.css']
})
export class DialogEditClassRoom{

    editPriceRoom(){
        const dialogRef = this.dialog.open(DialogEditPriceRoom,{width: '500px'});

        dialogRef.afterClosed().subscribe(result =>{
            console.log('edit price room');
        });
    }

    constructor(
        public dialogRef: MatDialogRef<DialogEditClassRoom>,
        public dialog: MatDialog,
    ){}
}

@Component({
    selector: 'dialog-edit-price-room',
    templateUrl: './dialog-edit-price-room.html',
    styleUrls: ['./dialog-setting.component.css']
})
export class DialogEditPriceRoom{

    deleteSetting(){
        const dialogRef = this.dialog.open(DialogDeleteSetting,{width: '500px'});
    
        dialogRef.afterClosed().subscribe(result => {
          console.log('delete price room');
        });
    }

    constructor(
        public dialogRef: MatDialogRef<DialogEditPriceRoom>,
        public dialog: MatDialog,
    ){}
}




