import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { LoginComponent } from './login/login.component';

import { SelectHotelComponent } from './header/select-hotel/select-hotel.component';
import { SettingComponent } from './header/setting/setting.component';
import { SettingContentComponent } from './header/setting/setting-content/setting-content.component';

import { AppMainComponent } from './app-main/app-main.component';
import { GeneralInformationComponent } from './app-main/general-information/general-information.component';
import { RoomMapComponent } from './app-main/room-map/room-map.component';

const appRoutes: Routes = [
  { 
    path: '', 
    redirectTo: 'dashboard',
    pathMatch: 'full'
  },
  { 
    path: 'login', 
    component: LoginComponent 
  },
  { 
    path: 'dashboard', 
    component: AppMainComponent, 
    children: [
      { 
        path: '', 
        redirectTo: 'thong-tin-chung',
        pathMatch: 'full'
      },
      //header
      { path: 'setting', component: SettingComponent },
      { path: 'select-hotel', component: SelectHotelComponent },
      { path: 'setting-content/:tabNumber',component: SettingContentComponent},
      //sidenav
      { path: 'thong-tin-chung', component: GeneralInformationComponent },
      { path: 'room-map', component: RoomMapComponent },
    ]
  },
]

@NgModule({
  imports: [RouterModule.forRoot(appRoutes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
