import { Injectable } from '@angular/core';
import {FormControl} from '@angular/forms';
import { MatDialog, MatDialogRef } from '@angular/material';
import { HttpClient } from '@angular/common/http';
import {CtyDialog,ThiTruongDialog,NguonDialog} from './header/them-khach/dialog/dialog-themkhach.component';

export interface ListService{
  name: string;
  price: number;
  SL: number;
}

@Injectable({
  providedIn: 'root'
})

export class MyService {

  companys: string[] = ['Công ty 1', 'Công ty 2', 'Công ty 3'];
  sources: string[] = ['Online', 'Công ty', 'Walk-in'];
  markets: string[] = ['Châu Á', 'Châu Âu', 'Châu Mỹ'];
  options: string[] = ['Ăn sáng', 'Đưa rước sân bay','Spa-massage thư giãn'];
  services: ListService[] = [
    {name:'Bia Heineken',price:20000, SL: 0},
    {name:'Bia Tiger',price:20000, SL: 0},
    {name:'Bia SG Special',price:17000, SL: 0},
    {name:'Nước suối',price:10000, SL: 0},
    {name:'Coca cola',price:15000, SL: 0},
    {name:'Pesi',price:15000, SL: 0},
    {name:'Sting',price:15000, SL: 0},
  ];

  getCompanys(){
    return this.companys;
  }
  getSources(){
    return this.sources;
  }
  getMarkets(){
    return this.markets;
  }
  getOptions(){
    return this.options;
  }
  getServices(){
    return this.services;
  }

  openAddCty(){
    const dialogRef = this.dialog.open(CtyDialog,{width: '90%'});

    dialogRef.afterClosed().subscribe(result => {
      console.log('add cty close');
    });
  }

  openaddNguon(){
    const dialogRef = this.dialog.open(NguonDialog,{maxWidth: '90%',width: '500px'});

    dialogRef.afterClosed().subscribe(result => {
      console.log('add nguon close');
    });
  }

  openaddThiTruong(){
    const dialogRef = this.dialog.open(ThiTruongDialog,{maxWidth: '90%',width: '500px'});

    dialogRef.afterClosed().subscribe(result => {
      console.log('add thi truong close');
    });
  }

  // getShippingPrices(){
  //   return this.http.get('/assets/list-menu.json');
  // }

  constructor(
    //private http: HttpClient
    public dialog: MatDialog,
  ) { }

}